.toggle-button {
  width: 40px;
  height: 24px;
  vertical-align: top;
  border: 1px solid var(--grey-title-color);
  cursor: pointer;
  appearance: none;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 30px;
  outline: none;
  margin: 0;
  display: inline-block;
  position: relative;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.toggle-button:after {
  content: "";
  width: 22px;
  height: 22px;
  background: #fff;
  border-radius: 50%;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(0);
}

.toggle-button:checked:after {
  background-color: #dcdcdc;
  transform: translateX(16px);
}

.toggle-button:checked {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

